import React from 'react'
import { TextField } from '@mui/material'

const styles = {
  search: {
    position: 'absolute',
    left: 37.5,
    top: 12.5,
    width: '250px',
    backgroundColor: '#fff',
    borderRadius: 5,
    zIndex: 2,
    outline: "none",
  }
}

const SearchComponent = () => {
  const [text, setText] = React.useState("")
  return (
    <div>
      <input 
        style={styles.search}
        type="text"
        name="Поиск"
        placeholder='Поиск'
        autoComplete="off"
      />
      <TextField 
        style={styles.search} 
        label="Поиск" 
        variant="outlined" 
        onChange={(txt) => setText(txt)}
      />
    </div>

  );
}

export default SearchComponent;